<script>
import PersonalInfo from "@/components/PersonalInfo.vue";
import Items from "@/components/Items.vue";
import { mapGetters, mapActions } from "vuex";

export default {
  name: "Info",
  components: {
    PersonalInfo,
    Items,
  },
  props: {
    msg: String,
  },
  data() {
    return {
      state: [],
      filteredDate: [],
      quantity: 1,
    };
  },
  computed: {
    ...mapGetters([
      "INFOUSER",
      "INFOPRODUCTS",
      "FILTER_VALUE",
      "ITEMLIST",
      "FROMTO",
    ]),
  },
  methods: {
    ...mapActions([
      "GET_INFO_FROM_API",
      "GET_FILTER_VALUE",
      "GET_LIST_ITEM",
      "DELETE_ITEM",
      "DICREMENT",
      "INCREMENT",
    ]),
    filterDate(value) {
      this.GET_FILTER_VALUE(value);
    },
    listOfItems(value) {
      this.GET_LIST_ITEM(value);
    },
    decrement(index) {
      this.DICREMENT(index);
    },
    increment(index) {
      this.INCREMENT(index);
    },
    filterItems(value) {
      let vm = this;
      vm.filteredDate = [...this.INFOPRODUCTS];
      if (value) {
        vm.filteredDate = vm.filteredDate.filter((item) => {
          return item.name.toLowerCase().includes(value.toLowerCase());
        });
      } else {
        vm.filteredDate = vm.INFOPRODUCTS;
      }
    },
    setItem(value, index) {
      let vm = this;

      vm.state = [...this.ITEMLIST];

      vm.state.forEach((item) => {
        item.modal = false;
      });
      //console.log(value.name);
      vm.state[index].name = value.name;
      vm.state[index].disassemble = value.disassemble;
      vm.state[index].assemble = value.assemble;
      vm.filteredDate = [];
    },
    addNewRow(name) {
      let vm = this;
      let id = vm.ITEMLIST.length;
      vm.ITEMLIST.push({
        id: id,
        name: name,
        qty: 1,
        disassemble: null,
        assemble: null,
        modal: false,
      });
      this.filteredDate = [];
    },
    deleteItem(index) {
      this.DELETE_ITEM(index);
    },
  },
  watch: {
    FILTER_VALUE() {
      this.filterItems(this.FILTER_VALUE);
    },
    ITEMLIST() {
      this.setItem(this.ITEMLIST);
    },
  },
  mounted() {
    this.GET_INFO_FROM_API().then((response) => {
      this.filterItems(this.FILTER_VALUE);
      if (response.data) {
        console.log("data arrived");
      }
    });
  },
};
</script>

